body {
  margin: 0;
  padding: 0; }

.slidecontainer {
  width: 100%;
  text-align: center;
  flex-grow: 1; }
  .slidecontainer p {
    display: inline;
    font-weight: bold;
    color: black; }

input[type=range]::-webkit-slider-thumb {
  box-shadow: 1px 1px 1px #000000, 0px 0px 1px #0d0d0d;
  border: 1px solid #000000;
  height: 20px;
  width: 10px;
  border-radius: 2px;
  background: #000000;
  cursor: pointer;
  -webkit-appearance: none;
  margin-top: -7px; }

input[type=range]::-moz-range-thumb {
  box-shadow: 1px 1px 1px #000000, 0px 0px 1px #0d0d0d;
  border: 1px solid #000000;
  height: 30px;
  width: 14px;
  border-radius: 2px;
  background: #000000;
  cursor: pointer; }

input[type=range]::-ms-thumb {
  box-shadow: 1px 1px 1px #000000, 0px 0px 1px #0d0d0d;
  border: 1px solid #000000;
  height: 30px;
  width: 14px;
  border-radius: 2px;
  background: #000000;
  cursor: pointer;
  height: 8.4px; }

.room-container {
  background-color: rgba(255, 191, 203, 0.95);
  padding-bottom: 2%; }

#room-title {
  text-shadow: -1px 0px 19px rgba(0, 0, 0, 0.62);
  color: white;
  text-align: center;
  font-size: 350%;
  font-family: 'Baloo Bhai'; }

.effects-slider-container {
  width: 100%;
  text-align: center;
  flex-grow: 1;
  text-shadow: 1px 1px #01fdf6; }
  .effects-slider-container label {
    display: inline;
    font-weight: bold;
    color: black; }
  .effects-slider-container h1 {
    text-shadow: 2px 2px #000000;
    color: white;
    padding-top: 5%; }

.slider {
  margin: 20px;
  display: inline;
  -webkit-appearance: none;
  -moz-appearance: none;
       appearance: none;
  width: 25%;
  height: 5px;
  background-color: #01fdf6; }

.slider:focus {
  outline: none; }

.slider::-webkit-slider-thumb {
  -webkit-appearance: none;
  /* Override default look */
  appearance: none;
  width: 20px;
  /* Set a specific slider handle width */
  height: 20px;
  /* Slider handle height */
  border-radius: 50%;
  background: darkgrey; }

.transport {
  margin: 16px;
  background-color: #DFE0DC;
  border: 1px solid darkgrey;
  display: inline;
  cursor: pointer;
  color: black;
  font-family: Arial;
  font-size: 20px;
  font-weight: bold;
  padding: 10px 20px;
  width: 5%;
  text-decoration: none;
  text-shadow: 0px 1px 0px #2b665e; }

.transport:hover {
  background-color: #01FDF6; }

.transport:focus {
  outline: none; }

.transport:active {
  position: relative; }

.row-container {
  display: flex;
  justify-content: center;
  box-sizing: content-box; }

.row-title {
  background-color: #DFE0DC;
  color: black;
  border: 1px solid darkgrey;
  border-collapse: collapse;
  padding: 1em;
  width: 7%;
  font-size: 10px;
  font-weight: bold;
  overflow: hidden; }

.tempo {
  text-shadow: 1px 1px #01fdf6; }

.row-tile {
  cursor: pointer;
  background-color: #DFE0DC;
  border: 1px solid darkgrey;
  border-collapse: collapse;
  width: 2%;
  align-items: center;
  padding: 1em; }

.selected {
  background-color: #01FDF6; }

.active-tile {
  opacity: 0.8;
  border: solid #FCF7FF;
  border-width: 1px 1px;
  border-collapse: collapse; }

.scale-controls-container {
  margin: 2em;
  width: 93%;
  text-align: center;
  flex-grow: 1;
  align-items: center;
  font-weight: bold;
  color: #DFE0DC; }

.scale-controls {
  margin: 2em;
  display: inline;
  width: 25%;
  height: 5px; }
  .scale-controls label {
    color: black;
    text-shadow: 1px 1px #01fdf6;
    margin: 1em; }

.chat-bar {
  font-size: 18px;
  padding-bottom: 10px;
  position: fixed;
  right: 0;
  top: 0;
  background-color: rgba(223, 224, 220, 0.7);
  height: 100vw; }

.main-chat {
  list-style-type: none;
  text-align: left;
  display: none;
  width: 15vw; }

.shrink-room {
  width: 75vw; }

.active {
  display: block;
  border: none; }

.chat-input {
  text-align: center;
  width: 14vw;
  height: 5vh;
  margin: 0 auto;
  position: fixed;
  right: 10px;
  bottom: 15px;
  color: black;
  text-align: left;
  border-radius: 0.5em; }

.chatbar-toggle {
  position: absolute;
  top: 36px;
  right: 36px;
  cursor: pointer;
  color: rgba(255, 255, 255, 0.8);
  font-size: 24px;
  z-index: 1; }

ol {
  list-style-type: none;
  overflow-y: scroll;
  margin-top: 7vh;
  margin-bottom: 6vh;
  max-height: 80vh;
  position: fixed;
  right: 5px;
  padding-left: 0.5em;
  background: white;
  border-bottom: 1px solid black;
  border-top: 1px solid black;
  padding-bottom: 1em;
  width: 14vw; }

.chat-messages {
  color: black; }

/* Position and sizing of burger button */
.bm-burger-button {
  position: fixed;
  width: 26px;
  height: 20px;
  left: 36px;
  top: 36px; }

/* Color/shape of burger icon bars */
.bm-burger-bars {
  background: white; }

/* Color/shape of burger icon bars on hover*/
.bm-burger-bars-hover {
  background: #a90000; }

/* Position and sizing of clickable cross button */
.bm-cross-button {
  height: 24px;
  width: 24px; }

/* Color/shape of close button cross */
.bm-cross {
  background: #bdc3c7; }

/*
Sidebar wrapper styles
Note: Beware of modifying this element as it can break the animations - you should not need to touch it in most cases
*/
.bm-menu-wrap {
  position: fixed;
  height: 100%; }

/* General sidebar styles */
.bm-menu {
  background: #373a47;
  padding: 2.5em 1.5em 0;
  font-size: 1.15em; }

/* Morph shape necessary with bubble or elastic */
.bm-morph-shape {
  fill: #373a47; }

/* Wrapper for item list */
.bm-item-list {
  color: #b8b7ad;
  padding: 0.8em; }

/* Individual item */
.bm-item {
  display: inline-block; }

/* Styling of overlay */
.bm-overlay {
  background: rgba(0, 0, 0, 0.3); }

.background {
  height: 100%;
  text-align: center; }

.sign-form {
  padding: 10px; }

.label-form {
  padding: 10px;
  margin: 5px;
  width: 300px;
  color: black; }

